import React, { useCallback, useEffect, useState, useRef } from 'react';
import axios from 'axios';
import paladino from '../../../img/visual/paladino.jpg';
import archer from '../../../img/visual/maya.jpg';

const Toolbar = ({ game }) => {
  const [players, setPlayers] = useState(8);
  const [civ1, setCiv1] = useState('');
  const [civ2, setCiv2] = useState('');
  const [selectedTeam, setSelectedTeam] = useState();

  const setResultModalRef = useRef();
  const changeMapModalRef = useRef();
  const newGameModalRef = useRef();

  const { Teams: teams, end, map, id, createdAt, picks } = game;

  const totalPicks = Object.values(picks['Team1'].picked).length + Object.values(picks['Team2'].picked).length;

  useEffect(() => {
    if (!teams || !teams.length || (teams[0].id !== selectedTeam && teams[1].id !== selectedTeam)) {
      setSelectedTeam();
    }
  }, [teams]);

  const onChangeHandler = ({ id }) => setSelectedTeam(id);

  const onSetResultHandler = useCallback(async () => {
    try {
      await axios.put(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game/${id}/winner`, {
        teamId: selectedTeam,
      });
      const modal = window.bootstrap.Modal.getInstance(setResultModalRef.current);
      modal.hide();
    } catch (e) {
      console.log(e);
    }
  }, [id, selectedTeam, setResultModalRef]);

  const onChangeMapHandler = useCallback(async () => {
    try {
      await axios.put(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game/${id}/map`, {});
      const modal = window.bootstrap.Modal.getInstance(changeMapModalRef.current);
      modal.hide();
    } catch (e) {
      console.log(e);
    }
  }, [id, changeMapModalRef]);

  const handlePlayersSelect = (value) => {
    setPlayers(value);
  };

  const handleCiv1 = (value) => {
    setCiv1(value);
  };

  const handleCiv2 = (value) => {
    setCiv2(value);
  };

  const newGameHandler = useCallback(async () => {
    try {
      await axios.post(
        `${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game`,
        {
          civs: [civ1, civ2],
          players,
        },
        { withCredentials: true },
      );
      const modal = window.bootstrap.Modal.getInstance(newGameModalRef.current);
      modal.hide();
    } catch (e) {
      console.log(e);
    }
  }, [newGameModalRef, civ1, civ2, players]);

  return (
    <div className="row mt-3 mb-4 ml-1 mr-1">
      <div className="col-md border-bottom ml-5 p-0 pb-2">
        <h4>
          <b>
            Partita del {new Date(createdAt).toLocaleDateString()}
            {end && <span> - Risultati registrati</span>}
          </b>
        </h4>
        <h6>
          {picks.players / 2} vs {picks.players / 2} - Mappa: Casuale di terra
          <span
            style={{ color: '#2A9CF4' }}
            data-bs-toggle="tooltip"
            data-bs-placement="bottom"
            title="Altopiano, Cenote, Fiumi, Mongolia, Oasi, Valle, Yucatan, Anello d'acqua, Città dei laghi"
          >
            <i className="far fa-question-circle fa-sm ml-1" />
          </span>
        </h6>
      </div>

      <div className="col-md-5 d-flex justify-content-end border-bottom mr-5 pt-3 pr-0">
        <div className="mr-4">
          <button
            type="button"
            className="btn btn-outline-dark border-0"
            data-bs-toggle="modal"
            data-bs-target="#newGame"
          >
            <i className="fas fa-plus mr-2" />
            <b>Nuova partita</b>
          </button>
        </div>
        <div className="mr-4">
          <button
            id="primary-disabled"
            type="button"
            className="btn btn-outline-dark border-0"
            data-bs-toggle="modal"
            data-bs-target="#setResoults"
            disabled={!teams.length || totalPicks !== picks.players || end}
          >
            <i className="fas fa-crown mr-2" />
            <b>Registra i risultati</b>
          </button>
        </div>
        <div>
          <button
            type="button"
            className="btn btn-outline-dark border-0"
            data-bs-toggle="modal"
            data-bs-target="#changeMap"
            disabled={end}
          >
            <i className="fas fa-sync-alt mr-2" />
            <b>Cambia mappa</b>
          </button>
        </div>
      </div>

      {/*Modal newGame*/}
      <div
        className="modal fade"
        id="newGame"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        ref={newGameModalRef}
      >
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title" id="exampleModalLabel">
                <b>Crea nuova partita</b>
              </h4>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">
              {!end && (
                <div className="alert alert-danger mt-2 mb-5" role="alert">
                  I risultati della partita precedente <b>non sono stati registrati</b> e cliccando su "Crea nuova
                  partita" verranno persi.
                </div>
              )}
              <div className="row m-4 mb-0">
                <div className="col-md-12">
                  <div className="row">
                    <div className="col-md-4">
                      <h6>
                        <b>Numero giocatori</b>
                      </h6>
                      <div className="form-check mb-2">
                        <input
                          className="form-check-input"
                          type="radio"
                          name="squadre"
                          value="4"
                          id="flexRadioDefault6"
                          checked={players === 4}
                          onChange={() => handlePlayersSelect(4)}
                        />
                        <label className="form-check-label" htmlFor="flexRadioDefault6">
                          2 vs 2
                        </label>
                      </div>
                      <div className="form-check mb-2">
                        <input
                          className="form-check-input"
                          type="radio"
                          name="squadre"
                          value="6"
                          id="flexRadioDefault7"
                          checked={players === 6}
                          onChange={() => handlePlayersSelect(6)}
                        />
                        <label className="form-check-label" htmlFor="flexRadioDefault7">
                          3 vs 3
                        </label>
                      </div>
                      <div className="form-check mb-2">
                        <input
                          className="form-check-input"
                          type="radio"
                          name="squadre"
                          value="8"
                          id="flexRadioDefault8"
                          checked={players === 8}
                          onChange={() => handlePlayersSelect(8)}
                        />
                        <label className="form-check-label" htmlFor="flexRadioDefault8">
                          4 vs 4
                        </label>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <h6>
                        <b>Popolazioni</b>
                      </h6>
                      <div className="dropdown disabled">
                        <button
                          className="btn disabled pl-0 dropdown-toggle"
                          type="button"
                          id="dropdownMenuButton"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        >
                          Tutte
                        </button>
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                          <li>
                            <a className="dropdown-item" href="#">
                              Lista1
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Lista2
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Lista3
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <h6>
                        <b>Tipologia di mappa</b>
                      </h6>
                      <div className="dropdown disabled">
                        <button
                          className="btn disabled pl-0 dropdown-toggle"
                          type="button"
                          id="dropdownMenuButton"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        >
                          Casuale di terra
                        </button>
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                          <li>
                            <a className="dropdown-item" href="#">
                              Casuale
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Casuale di terra
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Casuale di mare
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                  <div className="row mt-4">
                    <div className="col-md-12 border rounded p-4">
                      <h6>
                        <b>Popolazioni extra del giorno</b>
                      </h6>
                      <div className="d-inline-flex">
                        <div className="dropdown">
                          <button
                            className="btn pl-0 dropdown-toggle"
                            type="button"
                            id="dropdownMenuButton"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                            style={{ minWidth: '100px' }}
                          >
                            Pop. 1:&#x000A0;
                            {civ1}
                          </button>
                          <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Berberi')}>
                                Berberi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Birmani')}>
                                Birmani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Etiopi')}>
                                Etiopi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Khmer')}>
                                Khmer
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Malesi')}>
                                Malesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Maliani')}>
                                Maliani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Portoghesi')}>
                                Portoghesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Vietnamiti')}>
                                Vietnamiti
                              </a>
                            </li>
                          </ul>
                        </div>
                        <div className="dropdown pl-3">
                          <button
                            className="btn dropdown-toggle"
                            type="button"
                            id="dropdownMenuButton"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                          >
                            Pop. 2: &#x000A0;
                            {civ2}
                          </button>
                          <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Berberi')}>
                                Berberi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Birmani')}>
                                Birmani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Etiopi')}>
                                Etiopi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Khmer')}>
                                Khmer
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Malesi')}>
                                Malesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Maliani')}>
                                Maliani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Portoghesi')}>
                                Portoghesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv2('Vietnamiti')}>
                                Vietnamiti
                              </a>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="modal-footer border-0">
              <button type="button" className="btn btn-outline-secondary border-0" data-bs-dismiss="modal">
                Annulla
              </button>
              <button type="button" className="btn btn-primary" onClick={() => newGameHandler()}>
                Crea nuova partita
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*Modal setResoults*/}
      <div
        className="modal fade"
        id="setResoults"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        ref={setResultModalRef}
      >
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title" id="exampleModalLabel">
                <b>Registra i risultati di fine partita</b>
              </h4>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">
              {teams && (
                <div className="row m-4 mb-0">
                  <div className="col-md-8">
                    <span>
                      Seleziona il team <b>vincitore</b>:
                    </span>
                    <h6 className="mt-3 ml-2">
                      {!!teams[0] && (
                        <div className="form-check mb-2">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="winTeam"
                            id="radioTeam1"
                            value={teams[0].id}
                            checked={selectedTeam === teams[0].id}
                            onChange={() => onChangeHandler(teams[0])}
                          />
                          <label className="form-check-label" htmlFor="radioTeam1">
                            {teams[0].Players.map(({ nickname }) => nickname).join(', ')}
                          </label>
                        </div>
                      )}
                      {!!teams[1] && (
                        <div className="form-check  mb-2">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="winTeam"
                            id="radioTeam2"
                            value={teams[1].id}
                            checked={selectedTeam === teams[1].id}
                            onChange={() => onChangeHandler(teams[1])}
                          />
                          <label className="form-check-label" htmlFor="radioTeam2">
                            {teams[1].Players.map(({ nickname }) => nickname).join(', ')}
                          </label>
                        </div>
                      )}
                    </h6>
                  </div>
                  <div className="col-md-4 d-flex align-items-end flex-row-reverse p-0">
                    <img src={paladino} className="w-75" alt="Paladino" />
                  </div>
                </div>
              )}
            </div>
            {end && <p className="mt-3">Partita già registrata.</p>}
            {end && <p className="mt-3">Risultati della partita già salvati.</p>}
            <div className="modal-footer border-0">
              <button type="button" className="btn btn-outline-secondary border-0" data-bs-dismiss="modal">
                Annulla
              </button>
              <button
                type="button"
                className="btn btn-primary"
                disabled={end || !selectedTeam}
                onClick={onSetResultHandler}
              >
                Aggiorna la classifica
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*Modal changeMap*/}
      <div
        className="modal fade"
        id="changeMap"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        ref={changeMapModalRef}
      >
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title" id="exampleModalLabel">
                <b>Cambia mappa</b>
              </h4>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">
              <div className="row m-4 mb-0">
                <div className="col-md-12">
                  <span>
                    Prima di cambiare mappa, ti sei <b>confrontato</b> con gli altri giocatori?
                  </span>
                </div>
              </div>
            </div>
            <div className="modal-footer border-0">
              <button type="button" className="btn btn-outline-secondary border-0" data-bs-dismiss="modal">
                Annulla
              </button>
              <button type="button" className="btn btn-primary" onClick={() => onChangeMapHandler()}>
                Si, cambia mappa
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Toolbar;
