import React, { useCallback, useState, useEffect } from 'react';
import { Switch, Route, useHistory } from 'react-router-dom';
import axios from 'axios';
import { UserContext } from './context';
import loginPage from './components/login/structure/login_structure.js';
import nicknamePage from './components/login/nickname/nickname.js';
import Home from './components/home';
import './css/bootstrap.css';
import './css/style.css';

function App() {
  const [loggedUser, setLoggedUser] = useState(null);
  const history = useHistory();

  const getLastGame = useCallback(
    async () =>
      await axios.get(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game/last`, {
        withCredentials: true,
      }),
    [],
  );

  useEffect(async () => {
    try {
      const {
        data: { user },
      } = await getLastGame();
      setLoggedUser(user);
      history.push('/');
    } catch (e) {
      console.log(e);
      history.push('/login');
    }
  }, [getLastGame, history]);

  useEffect(() => {
    if (!!loggedUser && !loggedUser.nickname) {
      history.push('/nickname');
    }
  }, [history, loggedUser]);

  return (
    <UserContext.Provider value={loggedUser}>
      <div className="container-fluid">
        <Switch>
          <Route exact path="/login" component={loginPage} />
          <Route exact path="/nickname" component={nicknamePage} />
          {loggedUser && loggedUser.nickname && (
            <Route path="/">
              <Home />
            </Route>
          )}
        </Switch>
      </div>
    </UserContext.Provider>
  );
}

export default App;
